.add-record {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  max-width: 990px;
  min-width: 65%;
  padding: 20px;
}

.add-record h2 {
  text-align: left;
  margin: 5px 0 0 4%;
  font-size: 1.2em;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.87);
  font-family: Roboto, sans-serif;
}

.add-record fieldset {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  border: 0;
  margin-bottom: 10px;
}

.add-record .text-field,
.add-record .toggle {
  display: inline-block;
  min-width: 30%;
}

.add-record .text-field .error {
  color: red;
  text-align: left;
  position: relative;
  top: -9px;
}

.add-record .controls {
  margin-top: 20px;
}

.add-record .controls>div:first-of-type {
  margin-right: 20px;
}



